import { OnInit, ElementRef, ViewContainerRef } from '@angular/core';
import { Chart } from 'chart.js';
var ChartComponent = /** @class */ (function () {
    function ChartComponent(_elementRef, viewContainerRef) {
        this._elementRef = _elementRef;
        this.viewContainerRef = viewContainerRef;
        this.optionA = {
            legend: {
                display: false,
            },
            scales: {
                xAxes: [{
                        display: false,
                    }],
                yAxes: [{
                        display: true,
                        barThickness: 20,
                        offset: true,
                        gridLines: [{
                                display: false,
                            }],
                    }],
            },
        };
        this.optionB = {
            title: {
                display: true,
                text: ''
            },
            legend: {
                display: false,
            },
            scales: {
                xAxes: [{
                        display: true,
                        barThickness: 20,
                        offset: true,
                        ticks: {
                            padding: 10,
                        },
                    }],
                yAxes: [{
                        display: false,
                        ticks: {
                            suggestedMin: 5,
                            suggestedMax: 15,
                            callback: function (value, index, values) {
                                return value + '억원';
                            }
                        },
                    }],
            },
            tooltips: {
                callbacks: {
                    label: function (tooltipItem, data) {
                        var value = data.datasets[0].data[tooltipItem.index];
                        if (parseInt(value) >= 1000) {
                            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') + '원';
                        }
                        else {
                            return value + '원';
                        }
                    }
                }
            }
        };
        this.chartData = {
            infoDetail: [
                {
                    type: 'bar',
                    data: {
                        datasets: [{
                                data: [0, 0, 0, 0, 0],
                            }],
                        labels: ['', '', '', '', ''],
                    },
                    options: this.optionB,
                },
                {
                    type: 'bar',
                    data: {
                        datasets: [{
                                data: [9, 9.4, 10, 11, 11.4],
                            }],
                        labels: ['40평형', '45평형', '50평형', '60평형', '70평형'],
                    },
                    options: this.optionB,
                }
            ],
            siteDetail: [
                {
                    type: 'bar',
                    data: {
                        datasets: [{
                                data: [0, 0, 0, 0, 0],
                            }],
                        labels: ['', '', '', '', ''],
                    },
                    options: this.optionB,
                },
                {
                    type: 'bar',
                    data: {
                        datasets: [{
                                data: [9, 9.4, 10, 11, 11.4],
                            }],
                        labels: ['40평형', '45평형', '50평형', '60평형', '70평형'],
                    },
                    options: this.optionB,
                }
            ],
            analysisContents: [
                {
                    type: 'horizontalBar',
                    data: {
                        datasets: [{
                                data: ['500  ', '1200  ', '300  ']
                            }],
                        labels: ['PLATINUM  ', 'PREMIUM  ', 'STANDARD  '],
                    },
                    options: this.optionA,
                },
            ]
        };
    }
    ChartComponent.prototype.ngOnInit = function () {
        console.log('record:', this.record);
        var canvas = this._elementRef.nativeElement.querySelector('canvas');
        var ctx = canvas.getContext('2d');
        var fillPlatinum = ctx.createLinearGradient(0, 150, 0, 0);
        fillPlatinum.addColorStop(0, 'rgba(213, 177, 94, 0)');
        fillPlatinum.addColorStop(1, 'rgba(213, 177, 94, 1)');
        var fillPremium = ctx.createLinearGradient(0, 150, 0, 0);
        fillPremium.addColorStop(0, 'rgba(0, 0, 0, 0)');
        fillPremium.addColorStop(1, 'rgba(0, 0, 0, 1');
        var fillStandard = ctx.createLinearGradient(0, 150, 0, 0);
        fillStandard.addColorStop(0, 'rgba(170, 170, 170, 0)');
        fillStandard.addColorStop(1, 'rgba(170, 170, 170, 1)');
        this.chartData['infoDetail'][0].data.datasets[0]['backgroundColor'] =
            [fillStandard, fillStandard, fillStandard, fillPremium, fillPlatinum];
        this.chartData['infoDetail'][1].data.datasets[0]['backgroundColor'] =
            [fillStandard, fillStandard, fillStandard, fillPremium, fillPlatinum];
        var data = this.chartData[this.record.type][this.record.num];
        if (this.record.num === '0') {
            data.options.title.text = '현장 시세';
        }
        else if (this.record.num === '1') {
            data.options.title.text = '주위 시세';
        }
        data.data.labels = this.record.labels;
        data.data.datasets = this.record.datasets;
        data.data.datasets[0]['backgroundColor'] =
            [fillStandard, fillStandard, fillStandard, fillPremium, fillPlatinum];
        console.log('data at chart:', data);
        try {
            var chart = new Chart(canvas, data);
        }
        catch (e) {
        }
    };
    return ChartComponent;
}());
export { ChartComponent };



