import { OnInit } from '@angular/core';
import { Rankingpost1Directive, Rankingpost2Directive, Rankingpost3Directive } from '../../../directives/rankingpost.directive';
import { PostItem } from '../../../models/post-item';
import { PostingService } from '../../../services/posting.service';
import { ContentsService } from '../../../services/contents.service';
import { InfoCardComponent } from '../../../micro/info-card/info-card.component';
import { NewsCardComponent } from '../../../micro/news-card/news-card.component';
import { LawCardComponent } from '../../../micro/law-card/law-card.component';
var WeeklyComponent = /** @class */ (function () {
    function WeeklyComponent(contentsService, postingService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
    }
    WeeklyComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.contentsService.getWeeklyList('report').subscribe(function (data) {
            if (data) {
                // console.log('report: ', data);
                _this.addReportRecord(data);
            }
        });
        this.contentsService.getWeeklyList('news').subscribe(function (data) {
            if (data) {
                // console.log('news: ', data);
                _this.addNewsRecord(data);
            }
        });
        this.contentsService.getWeeklyList('law').subscribe(function (data) {
            if (data) {
                console.log('law: ', data);
                _this.addLawRecord(data);
            }
        });
    };
    WeeklyComponent.prototype.addReportRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                records[num]['rank'] = count[num];
                // console.log('record: ', records[record]);
                this.postingService.loadComponent(this.rankingpost1Directive.viewContainerRef, new PostItem(InfoCardComponent, records[num]));
            }
        }
    };
    WeeklyComponent.prototype.addNewsRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                records[num]['rank'] = count[num];
                // console.log('record: ', records[record]);
                this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(NewsCardComponent, records[num]));
            }
        }
    };
    WeeklyComponent.prototype.addLawRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                records[num]['rank'] = count[num]; // console.log('record: ', records[record]);
                this.postingService.loadComponent(this.rankingpost3Directive.viewContainerRef, new PostItem(LawCardComponent, records[num]));
            }
        }
    };
    return WeeklyComponent;
}());
export { WeeklyComponent };



