import { OnInit } from '@angular/core';
import { ContentsService } from '../../../services/contents.service';
import { PostingService } from '../../../services/posting.service';
import { PostItem } from '../../../models/post-item';
import { InfoCardComponent } from '../../../micro/info-card/info-card.component';
import { Rankingpost1Directive, Rankingpost2Directive } from '../../../directives/rankingpost.directive';
import { NewsCardComponent } from '../../../micro/news-card/news-card.component';
import { LawCardComponent } from '../../../micro/law-card/law-card.component';
import { NaverService } from '../../../services/naver.service';
var InfoMainComponent = /** @class */ (function () {
    function InfoMainComponent(contentsService, postingService, naverService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.naverService = naverService;
        this.sortType = 'date';
        this.hasMoreContents = true;
    }
    InfoMainComponent.prototype.ngOnInit = function () {
        this.getContentsListNewly(this.sortType, 1);
        this.getContentsListWeekly();
    };
    InfoMainComponent.prototype.getContentsListNewly = function (sort, id) {
        var _this = this;
        this.contentsService.getContentsList('info', 'newly', sort, id).subscribe(function (data) {
            if (data) {
                if (data.length === 0) {
                    _this.hasMoreContents = false;
                    return;
                }
                if (data.length !== 8) {
                    _this.hasMoreContents = false;
                }
                else {
                    _this.hasMoreContents = true;
                }
                console.log('data: ', data);
                var list_1 = [];
                data.forEach(function (content) {
                    list_1.push(content);
                });
                list_1.forEach(function (content) {
                    content['createdAt'] = new Date(content['createdAt']);
                    content['updatedAt'] = new Date(content['createdAt']);
                });
                _this.addNewlyRecord(list_1);
            }
        });
    };
    InfoMainComponent.prototype.getContentsListWeekly = function () {
        var _this = this;
        this.contentsService.getWeeklyList('info').subscribe(function (data) {
            if (data) {
                var list_2 = [];
                data.forEach(function (content) {
                    list_2.push(content);
                });
                list_2.forEach(function (content) {
                    content['createdAt'] = new Date(content['createdAt']);
                    content['updatedAt'] = new Date(content['createdAt']);
                });
                console.log('Weekly List: ', list_2);
                _this.WeeklyList = list_2;
                _this.addWeeklyRecord(_this.WeeklyList);
            }
        });
    };
    InfoMainComponent.prototype.paging = function (page) {
        // console.log('page: ', page);
        var container = this.rankingpost1Directive.viewContainerRef;
        container.clear();
        // this.addNewlyRecord(this.NewlyList.slice(start, start + 8));
        this.getContentsListNewly(this.sortType, page);
    };
    InfoMainComponent.prototype.sort = function (type) {
        var sortName = {
            '최근순': 'date',
            '클릭수': 'click',
            '댓글수': 'reply',
            '공유횟수': 'sns',
            '스크랩': 'scrap'
        };
        this.sortType = sortName[type];
        var container = this.rankingpost1Directive.viewContainerRef;
        container.clear();
        this.getContentsListNewly(this.sortType, 1);
    };
    InfoMainComponent.prototype.addNewlyRecord = function (records) {
        // tslint:disable-next-line:forin
        for (var record in records) {
            // console.log('record: ', records[record]);
            if (records[record]['c-type'] === '리포트') {
                this.postingService.loadComponent(this.rankingpost1Directive.viewContainerRef, new PostItem(InfoCardComponent, records[record]));
            }
            else if (records[record]['c-type'] === '부동산 뉴스') {
                this.postingService.loadComponent(this.rankingpost1Directive.viewContainerRef, new PostItem(NewsCardComponent, records[record]));
            }
            else if (records[record]['c-type'] === '법률 및 정책') {
                this.postingService.loadComponent(this.rankingpost1Directive.viewContainerRef, new PostItem(LawCardComponent, records[record]));
            }
        }
    };
    InfoMainComponent.prototype.addWeeklyRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                console.log('records[num]: ', records[num]);
                records[num]['rank'] = count[num];
                if (records[num]['c-type'] === '리포트') {
                    this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(InfoCardComponent, records[num]));
                }
                else if (records[num]['c-type'] === '부동산 뉴스') {
                    this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(NewsCardComponent, records[num]));
                }
                else if (records[num]['c-type'] === '법률 및 정책') {
                    this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(LawCardComponent, records[num]));
                }
            }
        }
    };
    return InfoMainComponent;
}());
export { InfoMainComponent };



