import { OnInit, EventEmitter } from '@angular/core';
var HorizontalListComponent = /** @class */ (function () {
    function HorizontalListComponent() {
        this.hasMoreContents = true;
        this.isNoNeed = false;
        // @Input() toptitle = 'hi';
        this.change = new EventEmitter();
        this.sorter = new EventEmitter();
        this.count = 1;
        // this._toptitle = this.toptitle;
    }
    HorizontalListComponent.prototype.ngOnInit = function () {
    };
    HorizontalListComponent.prototype.showMore = function () {
        this.count = this.count + 1;
        this.change.emit(this.count);
    };
    HorizontalListComponent.prototype.sort = function (event) {
        this.sorter.emit(event);
    };
    HorizontalListComponent.prototype.resetPage = function () {
        this.count = 1;
    };
    return HorizontalListComponent;
}());
export { HorizontalListComponent };



