import { OnInit } from '@angular/core';
import { Router } from '@angular/router';
var RankingListComponent = /** @class */ (function () {
    function RankingListComponent(router) {
        this.router = router;
        this.toptitle = 'noname';
    }
    RankingListComponent.prototype.ngOnInit = function () {
        // console.log('records form parent to ranking-list: ', this.records);
    };
    RankingListComponent.prototype.onClickMore = function () {
        this.router.navigate(['info/weekly']);
    };
    return RankingListComponent;
}());
export { RankingListComponent };



