import { OnInit, ViewContainerRef, ElementRef } from '@angular/core';
import { ContentsService } from '../../../services/contents.service';
import { ActivatedRoute } from '@angular/router';
import { environment } from '../../../../../environments/environment';
import { RankingpostDirective } from '../../../directives/rankingpost.directive';
import { NewsCardComponent } from '../../../micro/news-card/news-card.component';
import { PostingService } from '../../../services/posting.service';
import { PostItem } from '../../../models/post-item';
import { AuthService } from '../../../services/auth.service';
import { News } from '../../../models/news';
import { Meta } from '@angular/platform-browser';
import { FbShareService } from '../../../services/fb-share.service';
import { AlertService } from '../../../services/alert.service';
import { UserService } from '../../../services/user.service';
var NewsDetailComponent = /** @class */ (function () {
    function NewsDetailComponent(viewContainerRef, _elementRef, alertService, contentsService, userService, fbShareService, route, postingService, authService, meta) {
        this.viewContainerRef = viewContainerRef;
        this._elementRef = _elementRef;
        this.alertService = alertService;
        this.contentsService = contentsService;
        this.userService = userService;
        this.fbShareService = fbShareService;
        this.route = route;
        this.postingService = postingService;
        this.authService = authService;
        this.meta = meta;
        this.Data = new News();
        this.id = this.route.params['value'].id;
    }
    NewsDetailComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.route.params.subscribe(function (params) {
            _this.id = _this.route.params['value'].id;
            _this.updateDetail();
        });
        this.contentsService.getWeeklyList('news').subscribe(function (data) {
            if (data) {
                _this.RankingList = data;
                _this.addRankingRecord(_this.RankingList);
            }
        });
    };
    NewsDetailComponent.prototype.updateDetail = function () {
        var _this = this;
        this.contentsService.getContentsDetail('news', this.id).subscribe(function (data) {
            if (data) {
                _this.Data = data;
                // this.top.nativeElement.style.background = 'red';
                _this.background.nativeElement.src = environment.bucket.downloadUrl + _this.Data['slave-image1'];
                // this.top.nativeElement.style.backgroundSize = 'cover';
                // this.top.nativeElement.style.backgroundPosition = 'center';
                console.log('data: ', _this.Data);
                _this.meta.addTag({ name: 'og:url', content: 'we2win.com' });
                _this.meta.addTag({ name: 'og:title', content: _this.Data['title'] });
                _this.meta.addTag({ name: 'og:description', content: _this.Data['summary'] });
            }
        });
    };
    NewsDetailComponent.prototype.showMore = function (child) {
        child._elementRef.nativeElement.classList.add('show');
    };
    NewsDetailComponent.prototype.addRankingRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                records[num]['rank'] = count[num];
                // console.log('record: ', records[record]);
                this.postingService.loadComponent(this.rankingpostDirective.viewContainerRef, new PostItem(NewsCardComponent, records[num]));
            }
        }
    };
    NewsDetailComponent.prototype.bookmark = function () {
        if (!this.authService.isAuthenticated()) {
            this.alertService.error('로그인 하셔야 북마크 하실 수 있습니다.');
        }
        var bookmark = this._elementRef.nativeElement.querySelector('#bookmark');
        if (this.isBookmarked) {
            this.isBookmarked = false;
            bookmark.src = '/assets/img/icon_bookmark.png';
            bookmark.classList.remove('selected');
            this.alertService.warn('북마크가 해제되었습니다.');
            this.userService.removeBookmark(this.Data);
        }
        else {
            this.isBookmarked = true;
            bookmark.src = '/assets/img/icon_bookmark_selected.png';
            bookmark.classList.add('selected');
            this.alertService.success('북마크가 설정되었습니다.');
            this.userService.addBookmark(this.Data);
        }
    };
    NewsDetailComponent.prototype.fbShare = function () {
        this.fbShareService.share(environment.homeUrl + '/site/news/' + this.Data['no']);
    };
    return NewsDetailComponent;
}());
export { NewsDetailComponent };



