var NaverService = /** @class */ (function () {
    function NaverService() {
        this.started = false;
    }
    NaverService.prototype.create = function (naver) {
        this.Naver = naver;
        this.started = true;
        console.log('naverService successfully created.');
    };
    NaverService.prototype.isStarted = function () {
        return this.started;
    };
    NaverService.prototype.check = function () {
        var _this = this;
        // window.addEventListener('load', () => {
        console.log('starting..', this.Naver);
        this.Naver.getLoginStatus(function (status) {
            if (status) {
                /* (5) 필수적으로 받아야하는 프로필 정보가 있다면 callback처리 시점에 체크 */
                var email = _this.Naver.user.getEmail();
                console.log('email: ', email);
                // if (email == undefined || email == null) {
                //   alert('이메일은 필수정보입니다. 정보제공을 동의해주세요.');
                //   /* (5-1) 사용자 정보 재동의를 위하여 다시 네아로 동의페이지로 이동함 */
                //   naverLogin.reprompt();
                //   return;
                // }
                // this.router.navigate(['signup', 'form']);
            }
            else {
                console.log('callback 처리에 실패하였습니다.');
            }
        }, function (err) {
            console.log('error: ', err);
        });
        // });
    };
    NaverService.prototype.getUrl = function () {
        return this.Naver.generateAuthorizeUrl();
    };
    return NaverService;
}());
export { NaverService };



