import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { AlertService } from './alert.service';
import { AuthService } from './auth.service';
var ContentsService = /** @class */ (function () {
    function ContentsService(http, alertService, authService) {
        this.http = http;
        this.alertService = alertService;
        this.authService = authService;
    }
    ContentsService.prototype.getContentsList = function (page, list, sort, id) {
        return this.http.get(environment.apiUrl + '/contents/' + page + '/' + list + '/' + sort + '/' + (id || '1'))
            .map(function (res) { return res; });
    };
    ContentsService.prototype.getWeeklyList = function (page) {
        return this.http.get(environment.apiUrl + '/contents/' + page + '/weekly')
            .map(function (res) { return res; });
    };
    ContentsService.prototype.getContentsDetail = function (page, id) {
        var httpOptions = {};
        if (this.authService.isAuthenticated()) {
            httpOptions['headers'] = new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            });
        }
        return this.http.get(environment.apiUrl + '/detail/' + page + '/' + id, httpOptions)
            .map(function (res) { return res; });
    };
    ContentsService.prototype.addComments = function (body) {
        var bodyString = JSON.stringify(body);
        var headers = { headers: { 'Content-Type': 'application/json' } };
        return this.http.post(environment.apiUrl + '/contents/comments/', bodyString, headers).subscribe(function (res) { console.log(res); }, function (error) { console.log(error); });
    };
    ContentsService.prototype.getComments = function (cid) {
        return this.http.get(environment.apiUrl + '/contents/comments/' + cid)
            .map(function (res) { return res; });
    };
    ContentsService.prototype.getSimplesList = function (page, sort, id) {
        var httpOptions = {};
        if (this.authService.isAuthenticated()) {
            httpOptions['headers'] = new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            });
        }
        return this.http.get(environment.apiUrl + '/simples/' + page + '/' + sort + '/' + (id || '1'), httpOptions)
            .map(function (res) { return res; });
    };
    ContentsService.prototype.getRelatedList = function (page) {
        return this.http.get(environment.apiUrl + '/simples/' + page + '/weekly')
            .map(function (res) { return res; });
    };
    ContentsService.prototype.getSimplesDetail = function (page, id) {
        var httpOptions = {};
        if (this.authService.isAuthenticated()) {
            httpOptions['headers'] = new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            });
        }
        return this.http.get(environment.apiUrl + '/detail/' + page + '/' + id, httpOptions)
            .map(function (res) { return res; });
    };
    ContentsService.prototype.getFilePath = function (cid) {
        return this.http.get(environment.apiUrl + '/file/' + cid)
            .map(function (res) { return res; });
    };
    ContentsService.prototype.search = function (query) {
        return this.http.get(environment.apiUrl + '/search/' + query)
            .map(function (res) { return res; });
    };
    ContentsService.prototype.addRecruitContent = function (page, body) {
        var _this = this;
        var bodyString = JSON.stringify(body);
        var headers = { headers: { 'Content-Type': 'application/json' } };
        return this.http.put(environment.apiUrl + '/contents/' + page, bodyString, headers).subscribe(function (res) {
            console.log(res);
            _this.alertService.success('등록 완료했습니다.');
        }, function (error) {
            _this.alertService.error('오류가 발생했습니다.');
        });
    };
    return ContentsService;
}());
export { ContentsService };



