import { OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { AlertService } from '../../services/alert.service';
var AccountInfoComponent = /** @class */ (function () {
    function AccountInfoComponent(authService, alertService, router) {
        this.authService = authService;
        this.alertService = alertService;
        this.router = router;
        this.isLogin = false;
        this.showProfile = false;
        this.showMail = false;
        this.showNotification = false;
        this.user = {
            id: '-',
            name: '이름없음',
            point: '-',
            start: '',
            end: ''
        };
    }
    AccountInfoComponent.prototype.ngOnInit = function () {
        var _this = this;
        if (this.authService.isAuthenticated()) {
            this.isLogin = true;
            var userInfo = this.authService.getUserInfo();
            this.user.id = userInfo.user_id;
            this.user.point = userInfo.user_point;
            this.user.name = userInfo.user_name;
            this.user.start = userInfo.user_start;
            this.user.end = userInfo.user_end;
            console.log(this.user);
        }
        setTimeout(function () {
            if (!_this.authService.isAuthenticated()) {
                _this.isLogin = false;
            }
        }, 900000);
    };
    AccountInfoComponent.prototype.checkAdmin = function () {
        if (this.authService.isAuthenticated()) {
            this.router.navigate(['mng']);
        }
        else {
            this.error('관리자만 접속 가능합니다.');
        }
    };
    AccountInfoComponent.prototype.logout = function () {
        if (this.authService.logout()) {
            this.isLogin = false;
            this.showProfile = false;
        }
    };
    AccountInfoComponent.prototype.editProfile = function () {
        this.router.navigate(['/portfolio']);
        this.showProfile = false;
    };
    AccountInfoComponent.prototype.success = function (message) {
        this.alertService.success(message);
    };
    AccountInfoComponent.prototype.error = function (message) {
        this.alertService.error(message);
    };
    AccountInfoComponent.prototype.info = function (message) {
        this.alertService.info(message);
    };
    AccountInfoComponent.prototype.warn = function (message) {
        this.alertService.warn(message);
    };
    AccountInfoComponent.prototype.clear = function () {
        this.alertService.clear();
    };
    return AccountInfoComponent;
}());
export { AccountInfoComponent };



