import { OnInit } from '@angular/core';
import { ContentsService } from '../../services/contents.service';
import { PostingService } from '../../services/posting.service';
import { MypostDirective } from '../../directives/mypost.directive';
import { PostItem } from '../../models/post-item';
import { NewsCardComponent } from '../../micro/news-card/news-card.component';
import { SearchService } from '../../services/search.service';
import { LawCardComponent } from '../../micro/law-card/law-card.component';
import { SiteCardComponent } from '../../micro/site-card/site-card.component';
import { InfoCardComponent } from '../../micro/info-card/info-card.component';
var SearchComponent = /** @class */ (function () {
    function SearchComponent(contentsService, postingService, searchService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.searchService = searchService;
        this.hasMoreContents = true;
        this.isEmpty = false;
        this.reset = false;
    }
    SearchComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.searchService.getDataString().subscribe(function (data) {
            _this.Data = data.body;
            var container = _this.mypostDirective.viewContainerRef;
            console.log('data: ', data);
            if (_this.Data) {
                if (_this.Data.length) {
                    _this.isEmpty = false;
                    if (data.page === 1) {
                        container.clear();
                        _this.resetPage();
                        _this.hasMoreContents = true;
                    }
                    _this.addRecord(_this.Data);
                    if (_this.Data.length !== 8) {
                        _this.hasMoreContents = false;
                    }
                }
                else {
                    container.clear();
                    _this.isEmpty = true;
                }
            }
            else {
                container.clear();
                _this.isEmpty = true;
            }
        }, function (err) {
            console.log('err: ', err);
        });
    };
    SearchComponent.prototype.paging = function (page) {
        console.log('page: ', page);
        this.searchService.searchByPage(page);
    };
    SearchComponent.prototype.resetPage = function () {
        this.searchList.resetPage(1);
    };
    SearchComponent.prototype.addRecord = function (records) {
        var components = {
            '리포트': InfoCardComponent,
            '부동산 뉴스': NewsCardComponent,
            '법률 및 정책': LawCardComponent,
            '아파트': SiteCardComponent,
            '오피스텔': SiteCardComponent,
            '상가/호텔': SiteCardComponent,
            '토지': SiteCardComponent,
        };
        // tslint:disable-next-line:forin
        for (var record in records) {
            // console.log('record: ', records[record]);
            this.postingService.loadComponent(this.mypostDirective.viewContainerRef, new PostItem(components[records[record]['c-type']], records[record]));
        }
    };
    return SearchComponent;
}());
export { SearchComponent };



