import { Subject } from 'rxjs/Subject';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
var SearchService = /** @class */ (function () {
    function SearchService(http) {
        this.http = http;
        this.dataString = new Subject();
    }
    SearchService.prototype.updateData = function (data) {
        var _this = this;
        this.query = data;
        clearTimeout(window['setCount']);
        window['setCount'] = setTimeout(function () {
            console.log(_this.query);
            _this.search(_this.query, 1);
        }, 1000);
    };
    SearchService.prototype.getDataString = function () {
        return this.dataString;
    };
    SearchService.prototype.search = function (string, page) {
        var _this = this;
        var headers = { headers: { 'Content-Type': 'application/json' } };
        return this.http.get(environment.apiUrl + '/search/' + string + '/' + page, headers).subscribe(function (res) {
            _this.dataString.next({
                page: page,
                body: res['body']
            });
        }, function (err) {
            console.log('search error: ', err);
        });
    };
    SearchService.prototype.searchByPage = function (page) {
        this.search(this.query, page);
    };
    return SearchService;
}());
export { SearchService };



