import { OnInit } from '@angular/core';
import { ContentsService } from '../../../services/contents.service';
import { PostingService } from '../../../services/posting.service';
import { MypostDirective } from '../../../directives/mypost.directive';
import { PostItem } from '../../../models/post-item';
import { SiteCardComponent } from '../../../micro/site-card/site-card.component';
var CommercialComponent = /** @class */ (function () {
    function CommercialComponent(contentsService, postingService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.sortType = 'date';
        this.hasMoreContents = true;
    }
    CommercialComponent.prototype.ngOnInit = function () {
        this.getContentsList(this.sortType, 1);
    };
    CommercialComponent.prototype.getContentsList = function (sort, id) {
        var _this = this;
        this.contentsService.getContentsList('commercial', 'newly', sort, id).subscribe(function (data) {
            if (data) {
                console.log('data: ', data);
                _this.Data = data;
                _this.addRecord(_this.Data);
                if (data.length !== 8) {
                    _this.hasMoreContents = false;
                }
            }
        });
    };
    CommercialComponent.prototype.paging = function (page) {
        console.log('page: ', page);
        var container = this.mypostDirective.viewContainerRef;
        this.getContentsList(this.sortType, page);
    };
    CommercialComponent.prototype.sort = function (type) {
        var sortName = {
            '최근순': 'date',
            '클릭수': 'click',
            '댓글수': 'reply',
            '공유횟수': 'sns',
            '스크랩': 'scrap'
        };
        this.sortType = sortName[type];
        var container = this.mypostDirective.viewContainerRef;
        container.clear();
        this.getContentsList(this.sortType, 1);
    };
    CommercialComponent.prototype.addRecord = function (records) {
        // tslint:disable-next-line:forin
        for (var record in records) {
            // console.log('record: ', records[record]);
            this.postingService.loadComponent(this.mypostDirective.viewContainerRef, new PostItem(SiteCardComponent, records[record]));
        }
    };
    return CommercialComponent;
}());
export { CommercialComponent };



