import { OnInit, ViewContainerRef, ElementRef } from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { ContentsService } from '../../../services/contents.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PostingService } from '../../../services/posting.service';
import { RankingpostDirective } from '../../../directives/rankingpost.directive';
import { MypostDirective } from '../../../directives/mypost.directive';
import { PostItem } from '../../../models/post-item';
import { Report } from '../../../models/report';
import { environment } from '../../../../../environments/environment';
import { ChartComponent } from '../../../micro/chart/chart.component';
import { AuthService } from '../../../services/auth.service';
import { InfoCardComponent } from '../../../micro/info-card/info-card.component';
import { AlertService } from '../../../services/alert.service';
import { UserInfo } from '../../../models/userInfo';
import { UserService } from '../../../services/user.service';
import { FbShareService } from '../../../services/fb-share.service';
var InfoDetailComponent = /** @class */ (function () {
    function InfoDetailComponent(viewContainerRef, contentsService, postingService, alertService, userService, authService, fbShareService, route, router, meta, _elementRef) {
        this.viewContainerRef = viewContainerRef;
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.alertService = alertService;
        this.userService = userService;
        this.authService = authService;
        this.fbShareService = fbShareService;
        this.route = route;
        this.router = router;
        this.meta = meta;
        this._elementRef = _elementRef;
        this.Data = new Report();
        this.subImgUrl = ['', '', '', '', ''];
        this.selectedImgUrl = '';
        this.selectedNum = 1;
        this.showCharts = true;
        this.showMoreReport = false;
        this.isBookmarked = false;
        // userInfo;
        this.userInfo = new UserInfo();
        this.comments = [];
        this.id = this.route.params['value'].id;
    }
    InfoDetailComponent.prototype.ngOnInit = function () {
        var _this = this;
        // When routed to another info contents:
        this.route.params.subscribe(function (params) {
            _this.id = _this.route.params['value'].id;
            _this.showMoreReport = false;
            window.scrollTo(0, 0);
            _this.updateDetail();
        });
        // To get ranking report list.
        this.contentsService.getWeeklyList('report').subscribe(function (data) {
            if (data) {
                _this.RankingList = data;
                _this.addRankingRecord(_this.RankingList);
            }
        });
        if (this.authService.isAuthenticated()) {
            this.userInfo = this.authService.getUserInfo();
        }
    };
    InfoDetailComponent.prototype.updateDetail = function () {
        var _this = this;
        this.contentsService.getContentsDetail('report', this.id).subscribe(function (data) {
            if (data) {
                _this.Data = data;
                console.log('this.Data: ', _this.Data);
                if (_this.Data['current-duration1'] || _this.Data['around-duration1']) {
                    _this.showCharts = true;
                    _this.addChart();
                }
                else {
                    _this.showCharts = false;
                }
                _this.imgUrl = environment.bucket.downloadUrl + _this.Data['master-image'];
                _this.subImgUrl[1] = environment.bucket.downloadUrl + _this.Data['slave-image1'];
                _this.subImgUrl[2] = environment.bucket.downloadUrl + _this.Data['slave-image2'];
                _this.subImgUrl[3] = environment.bucket.downloadUrl + _this.Data['slave-image3'];
                _this.subImgUrl[4] = environment.bucket.downloadUrl + _this.Data['slave-image4'];
                _this.subImgUrl[5] = environment.bucket.downloadUrl + _this.Data['slave-image5'];
                _this.selectedImgUrl = environment.bucket.downloadUrl + _this.Data['slave-image1'];
                _this.meta.addTag({ name: 'og:url', content: 'we2win.com' });
                _this.meta.addTag({ name: 'og:title', content: _this.Data['title'] });
                _this.meta.addTag({ name: 'og:description', content: _this.Data['summary'] });
                _this.meta.addTag({ name: 'og:image', content: _this.imgUrl });
                _this.getComments();
            }
        });
    };
    InfoDetailComponent.prototype.selectImg = function (num) {
        this.selectedImgUrl = this.subImgUrl[num];
        this.selectedNum = num;
        this.imgNavigator.nativeElement.querySelectorAll('img').forEach(function (element) {
            element.classList.remove('show');
        });
        this.imgNavigator.nativeElement.querySelector('#i' + num).classList.add('show');
    };
    InfoDetailComponent.prototype.addChart = function () {
        // console.log('data: ', this.Data);
        var current = {
            type: 'infoDetail',
            num: '0',
            labels: [
                this.Data['current-duration1'],
                this.Data['current-duration2'],
                this.Data['current-duration3'],
                this.Data['current-duration4'],
                this.Data['current-duration5'],
            ],
            datasets: [{
                    data: [
                        parseInt(this.Data['current-amount1'], 10) * 10000000,
                        parseInt(this.Data['current-amount2'], 10) * 10000000,
                        parseInt(this.Data['current-amount3'], 10) * 10000000,
                        parseInt(this.Data['current-amount4'], 10) * 10000000,
                        parseInt(this.Data['current-amount5'], 10) * 10000000,
                    ]
                }],
            options: {}
        };
        var around = {
            type: 'infoDetail',
            num: '1',
            labels: [
                this.Data['around-duration1'],
                this.Data['around-duration2'],
                this.Data['around-duration3'],
                this.Data['around-duration4'],
                this.Data['around-duration5'],
            ],
            datasets: [{
                    data: [
                        parseInt(this.Data['around-amount1'], 10) * 10000000,
                        parseInt(this.Data['around-amount2'], 10) * 10000000,
                        parseInt(this.Data['around-amount3'], 10) * 10000000,
                        parseInt(this.Data['around-amount4'], 10) * 10000000,
                        parseInt(this.Data['around-amount5'], 10) * 10000000,
                    ]
                }]
        };
        var container = this.mypostDirective.viewContainerRef;
        container.clear();
        this.postingService.loadComponent(container, new PostItem(ChartComponent, current));
        this.postingService.loadComponent(container, new PostItem(ChartComponent, around));
    };
    InfoDetailComponent.prototype.addComment = function () {
        var _this = this;
        if (!this.authService.isAuthenticated()) {
            this.alertService.error('로그인 해주세요.');
            return false;
        }
        var body = {
            'c-id': this.Data['c-id'],
            'u-id': this.userInfo['user_id'],
            'date': new Date().toISOString(),
            'contents': this.NewComment.nativeElement.value
        };
        console.log('comment body: ', body);
        if (!body.contents) {
            this.alertService.error('댓글 내용이 없습니다.');
        }
        else {
            this.contentsService.addComments(body);
            // refresh current page
            var currentUrl_1;
            if (!this.router.url.split('#')[1]) {
                currentUrl_1 = this.router.url + '#commentBox';
            }
            else {
                currentUrl_1 = this.router.url;
            }
            this.router.navigateByUrl('/', { skipLocationChange: true }).then(function () {
                return _this.router.navigateByUrl(currentUrl_1);
            });
        }
    };
    InfoDetailComponent.prototype.getComments = function () {
        var _this = this;
        this.contentsService.getComments(this.Data['c-id']).subscribe(function (data) {
            // console.log('data: ', data);
            if (data.content[0]) {
                _this.comments = data.content;
                console.log('this.comments: ', _this.comments);
            }
        });
    };
    InfoDetailComponent.prototype.addRankingRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                records[num]['rank'] = count[num];
                // console.log('record: ', records[record]);
                this.postingService.loadComponent(this.rankingpostDirective.viewContainerRef, new PostItem(InfoCardComponent, records[num]));
            }
        }
    };
    InfoDetailComponent.prototype.showMore = function (child) {
        child._elementRef.nativeElement.classList.add('show');
    };
    InfoDetailComponent.prototype.bookmark = function () {
        if (!this.authService.isAuthenticated()) {
            this.alertService.error('로그인 하셔야 북마크 하실 수 있습니다.');
        }
        var bookmark = this._elementRef.nativeElement.querySelector('#bookmark');
        if (this.isBookmarked) {
            this.isBookmarked = false;
            bookmark.src = '/assets/img/icon_bookmark_black.png';
            bookmark.classList.remove('selected');
            this.alertService.warn('북마크가 해제되었습니다.');
            this.userService.removeBookmark(this.Data);
        }
        else {
            this.isBookmarked = true;
            bookmark.src = '/assets/img/icon_bookmark_black_selected.png';
            bookmark.classList.add('selected');
            this.alertService.success('북마크가 설정되었습니다.');
            this.userService.addBookmark(this.Data);
        }
    };
    InfoDetailComponent.prototype.fbShare = function () {
        this.fbShareService.share(environment.homeUrl + '/info/report/' + this.Data['no']);
    };
    return InfoDetailComponent;
}());
export { InfoDetailComponent };



