import { OnInit } from '@angular/core';
import { AuthService } from '../../../services/auth.service';
import { AlertService } from '../../../services/alert.service';
import { UserInfo, DetailedInfo } from '../../../models/userInfo';
var PersonalComponent = /** @class */ (function () {
    function PersonalComponent(authService, alertService) {
        this.authService = authService;
        this.alertService = alertService;
        this.userInfo = new UserInfo();
        this.detailedInfo = new DetailedInfo();
        this.hopeList = ['투자', '실거주', '모두 해당'];
    }
    PersonalComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.userInfo = this.authService.getUserInfo();
        this.authService.getUserInfoDetail(this.userInfo).subscribe(function (res) {
            _this.detailedInfo = res.user;
            console.log(_this.detailedInfo);
        }, function (err) { _this.alertService.error(err); });
    };
    return PersonalComponent;
}());
export { PersonalComponent };



