import { OnInit } from '@angular/core';
import { ContentsService } from '../../../services/contents.service';
import { PostingService } from '../../../services/posting.service';
import { MypostDirective } from '../../../directives/mypost.directive';
import { PostItem } from '../../../models/post-item';
import { EmployeeCardComponent } from '../../../micro/employee-card/employee-card.component';
var EmployeeComponent = /** @class */ (function () {
    function EmployeeComponent(contentsService, postingService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.sortType = 'date';
        this.hasMoreContents = true;
    }
    EmployeeComponent.prototype.ngOnInit = function () {
        this.getSimplesListNewly(this.sortType, 1);
    };
    EmployeeComponent.prototype.getSimplesListNewly = function (sort, id) {
        var _this = this;
        this.contentsService.getSimplesList('employee', sort, id).subscribe(function (data) {
            if (data) {
                console.log(data);
                _this.Data = data;
                _this.addRecord(_this.Data);
                if (data.length !== 8) {
                    _this.hasMoreContents = false;
                }
            }
        });
    };
    EmployeeComponent.prototype.paging = function (page) {
        console.log('page: ', page);
        var container = this.mypostDirective.viewContainerRef;
        this.getSimplesListNewly(this.sortType, page);
    };
    EmployeeComponent.prototype.sort = function (type) {
        var sortName = {
            '최근순': 'date',
            '클릭수': 'click',
            '댓글수': 'reply',
            '공유횟수': 'sns',
            '스크랩': 'scrap'
        };
        this.sortType = sortName[type];
        var container = this.mypostDirective.viewContainerRef;
        container.clear();
        this.getSimplesListNewly(this.sortType, 1);
    };
    EmployeeComponent.prototype.addRecord = function (records) {
        // tslint:disable-next-line:forin
        for (var record in records) {
            // console.log('record: ', records[record]);
            this.postingService.loadComponent(this.mypostDirective.viewContainerRef, new PostItem(EmployeeCardComponent, records[record]));
        }
    };
    return EmployeeComponent;
}());
export { EmployeeComponent };



