    FormComponent.prototype.hasId = function (e) {
        var _this = this;
        e.stopPropagation();
        var checkValue = this.signupForm.controls['u-id'].hasError('minlength')
            || this.signupForm.controls['u-id'].hasError('maxlength')
            || this.signupForm.controls['u-id'].hasError('required');
        if (checkValue) {
            this.alertService.info('4글자 ~ 15글자 이내로 적어주세요.');
            return false;
        }
        var userInfo = {
            'u-id': this.signupForm.controls['u-id'].value
        };
        // this.userService.hasId(userInfo);
        this.userService.hasId(userInfo)
            .subscribe(function (data) {
            if (data) {
                _this.checkId = true;
                _this.alertService.info('사용가능한 ID입니다.');
            }
            else {
                _this.checkId = false;
                _this.alertService.warn('이미 존재하는 ID입니다.');
            }
        }, function (error) {
            // console.log('error: ', error);
            _this.alertService.error('오류가 발생했습니다.');
        });
    };
    FormComponent.prototype.setLoginType = function (type) {
        if (this.loginType) {
            if (!confirm('로그인 방식을 변경하시겠습니까?')) {
                return;
            }
        }
        if (type) {
            // this.loginType = type;
            // sample code
            if (type === 'naver') {
                this.loginWithNaver();
                this.uId.nativeElement.readOnly = true;
                this.password.nativeElement.readOnly = true;
                this.passwordV.nativeElement.readOnly = true;
                this.name.nativeElement.readOnly = true;
                this.email.nativeElement.readOnly = true;
            }
            else if (type === 'kakao') {
                this.loginWithKakao();
            }
            else {
                this.signupForm.reset();
                this.loginType = 'google';
                this.uId.nativeElement.readOnly = false;
                this.password.nativeElement.readOnly = false;
                this.passwordV.nativeElement.readOnly = false;
                this.name.nativeElement.readOnly = false;
                this.email.nativeElement.readOnly = false;
            }
        }
    };
