    FormComponent.prototype.loginWithKakao = function () {
        var _this = this;
        // 로그인 창을 띄웁니다.
        window['Kakao'].Auth.login({
            success: function (authObj) {
                window['Kakao'].API.request({
                    url: '/v1/user/me',
                    success: function (authInfo) {
                        console.log('authInfo: ', authInfo);
                        _this.loginType = 'kakao';
                        var userInfo = {
                            'u-id': 'k_' + authInfo.id
                        };
                        // this.userService.hasId(userInfo);
                        _this.userService.hasId(userInfo)
                            .subscribe(function (data) {
                            if (data) {
                                _this.checkId = true;
                                _this.alertService.info('사용가능한 ID입니다.');
                            }
                            else {
                                _this.checkId = false;
                                _this.alertService.warn('이미 존재하는 ID입니다.');
                            }
                        }, function (error) {
                            // console.log('error: ', error);
                            _this.alertService.error('오류가 발생했습니다.');
                        });
                        _this.signupForm.controls['u-id'].setValue('k_' + authInfo.id);
                        _this.signupForm.controls['password'].setValue('KAKAO1234!');
                        _this.signupForm.controls['passwordV'].setValue('KAKAO1234!');
                        _this.signupForm.controls['name'].setValue(authInfo.properties.nickname);
                        _this.signupForm.controls['email'].setValue(authInfo.kaccount_email);
                        _this.uId.nativeElement.readOnly = true;
                        _this.password.nativeElement.readOnly = true;
                        _this.passwordV.nativeElement.readOnly = true;
                        _this.name.nativeElement.readOnly = true;
                        _this.email.nativeElement.readOnly = true;
                    },
                    fail: function (error) {
                        this.alertService.error('카카오 계정 정보 불러오기에 실패하였습니다.');
                    }
                });
                // this.signupForm.controls['u-id'].setValue('K' + new Date().toISOString().replace(/-/g, '').slice(2, 17));
                // this.signupForm.controls['password'].setValue('kakao1234!');
            },
            fail: function (err) {
                _this.alertService.error('카카오 계정 인증에 실패하였습니다.');
            }
        });
    };
    FormComponent.prototype.setLevel = function (level) {
        var levelName = ['STANDARD', 'PREMIUM', 'PLATINUM'];
        this.level = levelName[level - 1];
    };
    FormComponent.prototype.setLevelOnKeyDown = function (level, event) {
        if (event.keyCode === 0 || event.keyCode === 32 || event.keyCode === 13) {
            event.preventDefault();
            this.setLevel(level);
        }
    };
    FormComponent.prototype.setHyphen = function (input) {
        if (input === 'cp') {
            var str = this.signupForm.controls['cp'].value.replace(/\-/g, '');
            console.log(str);
            if (str.length === 11) {
                str = str.substring(0, 3) + '-' + str.substring(3, 7) + '-' + str.substring(7, str.length);
            }
            else if (str.length === 10) {
                str = str.substring(0, 3) + '-' + str.substring(3, 6) + '-' + str.substring(6, str.length);
            }
            this.signupForm.controls['cp'].setValue(str);
        }
    };
