import { ComponentFactoryResolver, ViewContainerRef } from '@angular/core';
var PostingService = /** @class */ (function () {
    function PostingService(componentFactoryResolver) {
        this.componentFactoryResolver = componentFactoryResolver;
    }
    PostingService.prototype.loadComponent = function (viewContainerRef, postItem) {
        var componentFactory = this.componentFactoryResolver
            .resolveComponentFactory(postItem.component);
        // viewContainerRef.clear();
        var componentRef = viewContainerRef.createComponent(componentFactory);
        var myPost = componentRef.instance;
        myPost.record = postItem.data;
    };
    PostingService.prototype.loadChart = function (viewContainerRef, postItem) {
        var componentFactory = this.componentFactoryResolver
            .resolveComponentFactory(postItem.component);
        // viewContainerRef.clear();
        var componentRef = viewContainerRef.createComponent(componentFactory);
        var myPost = componentRef.instance;
        myPost.record = postItem.data;
    };
    return PostingService;
}());
export { PostingService };



