import { OnInit } from '@angular/core';
import { ContentsService } from '../../../services/contents.service';
import { PostingService } from '../../../services/posting.service';
import { PostItem } from '../../../models/post-item';
import { SiteCardComponent } from '../../../micro/site-card/site-card.component';
import { InfoCardComponent } from '../../../micro/info-card/info-card.component';
import { Rankingpost1Directive, Rankingpost2Directive, Rankingpost3Directive } from '../../../directives/rankingpost.directive';
var SiteMainComponent = /** @class */ (function () {
    function SiteMainComponent(contentsService, postingService) {
        this.contentsService = contentsService;
        this.postingService = postingService;
        this.sortType1 = 'date';
        this.sortType2 = 'date';
        this.hasMoreContents1 = true;
        this.hasMoreContents2 = true;
    }
    SiteMainComponent.prototype.ngOnInit = function () {
        this.getContentsListNewly(this.sortType1, 1);
        this.getContentsListReporter(this.sortType2, 1);
        this.getContentsListWeekly();
    };
    SiteMainComponent.prototype.getContentsListNewly = function (sort, id) {
        var _this = this;
        this.contentsService.getContentsList('site', 'newly', sort, id).subscribe(function (data) {
            if (data) {
                if (data.length === 0) {
                    _this.hasMoreContents1 = false;
                    return;
                }
                else if (data.length !== 8) {
                    _this.hasMoreContents1 = false;
                }
                else {
                    _this.hasMoreContents1 = true;
                }
                console.log('Newly List: ', data);
                _this.NewlyList = data;
                _this.addNewlyRecord(_this.NewlyList);
            }
        });
    };
    SiteMainComponent.prototype.getContentsListReporter = function (sort, id) {
        var _this = this;
        this.contentsService.getContentsList('report', 'reporter', sort, id).subscribe(function (data) {
            if (data) {
                if (data.length === 0) {
                    _this.hasMoreContents2 = false;
                    return;
                }
                else if (data.length !== 8) {
                    _this.hasMoreContents2 = false;
                }
                else {
                    _this.hasMoreContents2 = true;
                }
                console.log('Report List: ', data);
                _this.ReportList = data;
                _this.addReporterRecord(_this.ReportList);
            }
        });
    };
    SiteMainComponent.prototype.getContentsListWeekly = function () {
        var _this = this;
        this.contentsService.getWeeklyList('site').subscribe(function (data) {
            if (data) {
                console.log('Weekly List: ', data);
                _this.WeeklyList = data;
                _this.addWeeklyRecord(_this.WeeklyList);
            }
        });
    };
    SiteMainComponent.prototype.paging1 = function (page) {
        this.getContentsListNewly(this.sortType1, page);
    };
    SiteMainComponent.prototype.paging2 = function (page) {
        this.getContentsListReporter(this.sortType2, page);
    };
    SiteMainComponent.prototype.sort1 = function (type) {
        var sortName = {
            '최근순': 'date',
            '클릭수': 'click',
            '댓글수': 'reply',
            '공유횟수': 'sns',
            '스크랩': 'scrap'
        };
        this.sortType1 = sortName[type];
        this.getContentsListNewly(this.sortType1, 1);
    };
    SiteMainComponent.prototype.sort2 = function (type) {
        var sortName = {
            '최근순': 'date',
            '클릭수': 'click',
            '댓글수': 'reply',
            '공유횟수': 'sns',
            '스크랩': 'scrap'
        };
        this.sortType2 = sortName[type];
        this.getContentsListReporter(this.sortType2, 1);
    };
    SiteMainComponent.prototype.addNewlyRecord = function (records) {
        var container = this.rankingpost1Directive.viewContainerRef;
        container.clear();
        // tslint:disable-next-line:forin
        for (var record in records) {
            this.postingService.loadComponent(container, new PostItem(SiteCardComponent, records[record]));
        }
    };
    SiteMainComponent.prototype.addReporterRecord = function (records) {
        var container = this.rankingpost2Directive.viewContainerRef;
        container.clear();
        this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(InfoCardComponent, records[0]));
        this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(InfoCardComponent, records[1]));
        this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(InfoCardComponent, records[2]));
        this.postingService.loadComponent(this.rankingpost2Directive.viewContainerRef, new PostItem(InfoCardComponent, records[3]));
    };
    SiteMainComponent.prototype.addWeeklyRecord = function (records) {
        var count = ['first', 'second', 'third'];
        for (var num in count) {
            if (records[num]) {
                records[num]['rank'] = count[num];
                // console.log('record: ', records[record]);
                this.postingService.loadComponent(this.rankingpost3Directive.viewContainerRef, new PostItem(SiteCardComponent, records[num]));
            }
        }
    };
    return SiteMainComponent;
}());
export { SiteMainComponent };



