import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { environment } from '../../../environments/environment.prod';
import { JwtHelper } from 'angular2-jwt';
import { AuthService } from './auth.service';
var UserService = /** @class */ (function () {
    function UserService(http, jwtHelper, authService) {
        this.http = http;
        this.jwtHelper = jwtHelper;
        this.authService = authService;
    }
    // getAll () {
    //   // return this.http.get<User[]>('/api');
    //   return this.http.get('/api/v1/infos')
    //     .map(data => { console.log('map: ', data); });
    // }
    // getById(id:
    //    string) {
    //   return this.http.get('/api/v1/infos');
    // }
    UserService.prototype.create = function (user) {
        var _this = this;
        console.log('user form create(): ', user);
        var bodyString = JSON.stringify(user);
        var headers = { headers: { 'Content-Type': 'application/json' } };
        return this.http.post(environment.apiUrl + '/users', bodyString, headers)
            .map(function (res) { return res; })
            .catch(function (error) { return _this.handleError(error.message); });
    };
    // update(user: UserInfo) {
    //   return this.http.put('/api/v1/infos', user);
    // }
    // delete(id: string) {
    //   return this.http.delete('/api/v1/infos');
    // }
    UserService.prototype.hasId = function (user) {
        console.log('user from hasId(): ', user);
        var bodyString = JSON.stringify(user);
        var headers = { headers: { 'Content-Type': 'application/json' } };
        return this.http.post(environment.apiUrl + '/hasid', bodyString, headers)
            .map(function (data) { return data.available; })
            .catch(function (error) { return Observable.throw(error.message); });
    };
    UserService.prototype.testing = function () {
        console.log('testing...');
        return this.http.get(environment.apiUrl + '/testing')
            .map(function (data) { console.log('testing(): ', data); });
    };
    UserService.prototype.getBookmark = function (type) {
        var httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            })
        };
        return this.http.get(environment.apiUrl + '/bookmark/' + type, httpOptions);
    };
    UserService.prototype.addBookmark = function (body) {
        var httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            })
        };
        return this.http.put(environment.apiUrl + '/bookmark/add', body, httpOptions).subscribe(function (res) { console.log(res); }, function (error) { console.log(error); });
    };
    UserService.prototype.removeBookmark = function (body) {
        var httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            })
        };
        return this.http.post(environment.apiUrl + '/bookmark/remove', body, httpOptions).subscribe(function (res) { console.log(res); }, function (error) { console.log(error); });
    };
    UserService.prototype.getSchedule = function () {
        var httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            })
        };
        return this.http.get(environment.apiUrl + '/schedule', httpOptions);
    };
    UserService.prototype.addSchedule = function (body) {
        var httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            })
        };
        return this.http.put(environment.apiUrl + '/schedule/add', body, httpOptions).subscribe(function (res) { console.log(res); }, function (error) { console.log(error); });
    };
    UserService.prototype.removeSchedule = function (body) {
        var httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': this.authService.getToken()
            })
        };
        return this.http.post(environment.apiUrl + '/schedule/remove', body, httpOptions).subscribe(function (res) { console.log(res); }, function (error) { console.log(error); });
    };
    UserService.prototype.handleError = function (error) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', error.error.message);
        }
        else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error("Backend returned code " + error.status + ", " +
                ("body was: " + error.error));
        }
        // return an ErrorObservable with a user-facing error message
        return new ErrorObservable('Something bad happened; please try again later.');
    };
    return UserService;
}());
export { UserService };



