import { OnInit, OnChanges, EventEmitter } from '@angular/core';
var VerticalListComponent = /** @class */ (function () {
    function VerticalListComponent() {
        this.isBunyang = false;
        this.toptitle = '무제';
        this.hasMoreContents = true;
        this.change = new EventEmitter();
        this.sorter = new EventEmitter();
        this.count = 1;
        this._toptitle = this.toptitle;
    }
    VerticalListComponent.prototype.ngOnInit = function () {
        if (this.toptitle === '분양 리포트') {
            this.isBunyang = true;
        }
    };
    VerticalListComponent.prototype.increment = function () {
        // console.log('count in vertical-list: ', this.count);
        this.count = (this.count + 1);
        this.change.emit(this.count);
        // console.log(this.contents.nativeElement.innerHTML);
        // this.contents.nativeElement.innerHTML = '';
    };
    VerticalListComponent.prototype.decrement = function () {
        this.count = (this.count - 1);
        this.change.emit(this.count);
    };
    VerticalListComponent.prototype.sort = function (event) {
        this.sorter.emit(event);
    };
    VerticalListComponent.prototype.ngOnChanges = function () {
    };
    return VerticalListComponent;
}());
export { VerticalListComponent };



