import { OnInit, Renderer, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FormControl } from '@angular/forms';
import { JwtHelper } from 'angular2-jwt';
import { AlertService } from '../../services/alert.service';
import { environment } from '../../../../environments/environment';
// yimport { Observable } from 'rxjs/Observable';
var LoginComponent = /** @class */ (function () {
    function LoginComponent(authService, fb, router, jwtHelper, alertService, renderer, elementRef) {
        this.authService = authService;
        this.fb = fb;
        this.router = router;
        this.jwtHelper = jwtHelper;
        this.alertService = alertService;
        this.renderer = renderer;
        this.elementRef = elementRef;
        this.TOKEN_NAME = 'jwt_token';
        this.loginForm = fb.group({
            'u-id': [null, Validators.required],
            'password': [null, Validators.required]
        });
    }
    LoginComponent.prototype.ngOnInit = function () {
        if (this.authService.isAuthenticated()) {
            this.router.navigate(['/']);
        }
        // this.info('로그인을 해주세요');
        this.loginForm = new FormGroup({
            'u-id': new FormControl('', [Validators.required]),
            'password': new FormControl('', [Validators.required]),
        });
    };
    LoginComponent.prototype.ngAfterViewInit = function () {
        var _this = this;
        this.naverLogin = new window['naver'].LoginWithNaverId({
            clientId: environment.naver.clientId,
            callbackUrl: environment.naver.callbackUrl,
            isPopup: true,
            loginButton: { color: 'green', type: 4, height: 50 } /* 로그인 버튼의 타입을 지정 */
        });
        /* 설정정보를 초기화하고 연동을 준비 */
        this.naverLogin.init();
        this.naverLogin.getLoginStatus(function (status) {
            if (status) {
                /* 필수적으로 받아야하는 프로필 정보가 있다면 callback처리 시점에 체크 */
                var email = this.naverLogin.user.getEmail();
                console.log('email: ', email);
                if (email == undefined || email == null) {
                    alert('이메일은 필수정보입니다. 정보제공을 동의해주세요.');
                    /* 사용자 정보 재동의를 위하여 다시 네아로 동의페이지로 이동함 */
                    this.naverLogin.reprompt();
                    return;
                }
                window.location.replace('처리후 되돌아갈 곳');
            }
            else {
                console.log('callback 처리에 실패하였습니다.');
            }
        });
        if (!window['Kakao'].Auth) {
            window['Kakao'].init(environment.kakao.clientId);
        }
        // 카카오 로그인 버튼을 생성합니다.
        window['Kakao'].Auth.createLoginButton({
            container: '#kakaoIdLogin',
            success: function (authObj) {
                window['Kakao'].API.request({
                    url: '/v1/user/me',
                    success: function (authInfo) {
                        var user = {
                            'user_id': 'k_' + authInfo.id,
                        };
                        console.log('authInfo: ', authInfo);
                        _this.authService.loginWithKakao(user).subscribe(function (auth) {
                            console.log('auth: ', auth);
                            if (auth) {
                                _this.router.navigate(['/']);
                            }
                            else {
                                _this.error('회원 정보를 불러오지 못했습니다.');
                                // this.error('아이디 또는 비밀번호가\n맞지 않습니다.');
                            }
                        }, function (err) {
                            if (err.status === 422) {
                                _this.error('아이디 또는 비밀번호가\n맞지 않습니다.');
                            }
                            else {
                                _this.error('로그인 중 오류가\n발생했습니다.');
                            }
                        });
                    },
                    fail: function (err) {
                        _this.error(JSON.stringify(err));
                    }
                });
            },
            fail: function (err) {
                _this.error(JSON.stringify(err));
            }
        });
    };
    LoginComponent.prototype.setLoginType = function (loginType) {
        this.loginType = loginType;
        if (this.loginType === 'naver') {
            // alert('네이버 로그인 팝업');
            // this.router.navigate(['/']);
            this.loginWithNaver();
        }
        else if (this.loginType === 'kakao') {
            // this.info('카카오 로그인 팝업');
            // this.router.navigate(['/']);
        }
    };
    LoginComponent.prototype.onSubmit = function (user) {
        var _this = this;
        // console.log(user);
        if (this.loginForm.valid) {
            user = this.loginForm.value;
            user.isLogin = true;
            this.authService.login(user).subscribe(function (auth) {
                // console.log(auth);
                if (auth) {
                    _this.router.navigate(['/']);
                }
                else {
                    _this.error('아이디 또는 비밀번호가\n맞지 않습니다.');
                }
            }, function (err) {
                if (err.status === 422) {
                    _this.error('아이디 또는 비밀번호가\n맞지 않습니다.');
                }
                else {
                    _this.error('로그인 중 오류가\n발생했습니다.');
                }
            });
        }
        else {
            this.error('아이디 또는 비밀번호를\n입력하지 않으셨습니다.');
        }
    };
    LoginComponent.prototype.loginWithNaver = function () {
        // this.naverLogin.getLoginStatus(function (status) {
        //   if (status) {
        //     /* (5) 필수적으로 받아야하는 프로필 정보가 있다면 callback처리 시점에 체크 */
        //     const email = this.naverLogin.user.getEmail();
        //     if (email == undefined || email == null) {
        //       alert("이메일은 필수정보입니다. 정보제공을 동의해주세요.");
        //       /* (5-1) 사용자 정보 재동의를 위하여 다시 네아로 동의페이지로 이동함 */
        //       this.naverLogin.reprompt();
        //       return;
        //     }
        //     window.location.replace("http://" + window.location.hostname + ((location.port == "" || location.port == undefined) ? "" : ":" + location.port) + "/sample/main.html");
        //   } else {
        //     console.log("callback 처리에 실패하였습니다.");
        //   }
        // });
        var naver_id_login = new window['naver_id_login']('Wubf2TBn_Q6UWKJRyT1Y', 'http://localhost:4200/callback');
        var state = naver_id_login.getUniqState();
        naver_id_login.setDomain(environment.homeUrl);
        // naver_id_login.setDomain('http://localhost:4200/callback');
        naver_id_login.setState(state);
        naver_id_login.setButton('white', 2, 40);
        naver_id_login.init_naver_id_login();
    };
    LoginComponent.prototype.success = function (message) {
        this.alertService.success(message);
    };
    LoginComponent.prototype.error = function (message) {
        this.alertService.error(message);
    };
    LoginComponent.prototype.info = function (message) {
        this.alertService.info(message);
    };
    LoginComponent.prototype.warn = function (message) {
        this.alertService.warn(message);
    };
    LoginComponent.prototype.clear = function () {
        this.alertService.clear();
    };
    return LoginComponent;
}());
export { LoginComponent };



