import { OnInit, ElementRef, ViewContainerRef } from '@angular/core';
import { FbShareService } from '../../services/fb-share.service';
import { environment } from '../../../../environments/environment';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { ContentsService } from '../../services/contents.service';
import { AlertService } from '../../services/alert.service';
import { UserService } from '../../services/user.service';
var LawCardComponent = /** @class */ (function () {
    function LawCardComponent(_elementRef, viewContainerRef, fbShareService, alertService, userService, authService, contentsService, router) {
        this._elementRef = _elementRef;
        this.viewContainerRef = viewContainerRef;
        this.fbShareService = fbShareService;
        this.alertService = alertService;
        this.userService = userService;
        this.authService = authService;
        this.contentsService = contentsService;
        this.router = router;
        this.level = 'standard';
        this.imageUrl = '/assets/img/icon_document.png';
    }
    LawCardComponent.prototype.ngOnInit = function () {
        // console.log(this.record);
        if (this.record['file']) {
            switch (this.record['file'].split('.')[1]) {
                case 'jpg':
                case 'png':
                case 'jpeg':
                case 'gif':
                    this.imageUrl = environment.bucket.downloadUrl + this.record['file'];
                    break;
            }
        }
        this._elementRef.nativeElement.classList.add(this.record['level'].toLowerCase());
    };
    LawCardComponent.prototype.bookmark = function () {
        if (!this.authService.isAuthenticated()) {
            this.alertService.error('로그인 하셔야 북마크 하실 수 있습니다.');
        }
        var bookmark = this._elementRef.nativeElement.querySelector('#bookmark');
        if (bookmark.classList.contains('selected')) {
            bookmark.src = '/assets/img/icon_bookmark.png';
            bookmark.classList.remove('selected');
            this.alertService.warn('북마크가 해제되었습니다.');
            this.userService.removeBookmark(this.record);
        }
        else {
            bookmark.src = '/assets/img/icon_bookmark_selected.png';
            bookmark.classList.add('selected');
            this.alertService.success('북마크가 설정되었습니다.');
            this.userService.addBookmark(this.record);
        }
    };
    LawCardComponent.prototype.fbShare = function () {
        this.alertService.warn('법률 및 정책 파일은 공유할 수 없습니다.');
    };
    LawCardComponent.prototype.download = function () {
        if (this.authService.isAuthenticated()) {
            this.contentsService.getFilePath(this.record['c-id']).subscribe(function (data) {
                console.log('file data:', data['content']['file']);
                window.location.assign(environment.bucket.downloadUrl + data['content']['file']);
            });
        }
        else {
            this.router.navigate(['login']);
        }
    };
    return LawCardComponent;
}());
export { LawCardComponent };



